<style scoped>
    .pages {
        text-align: left;
        padding: 0 20px;

    }

    .form {
        width: 100%;
        margin-top: 30px;
    }

    .hint {
        color: #969696;
        margin-left: 20px
    }
</style>

<template>
    <div class="pages">
        <H3>基础信息</H3>
        <div class="line"></div>
        <el-form class="form" ref="form" :model="form" label-width="120px">
            <el-form-item label="活动名称">
                <el-input style="width: 500px" v-model="form.title" placeholder="请输入"></el-input>

            </el-form-item>
            <el-form-item label="报名时间">
                <el-date-picker
                        style="width: 500px"
                        type="datetimerange"
                        range-separator="至"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        v-model="applyTimes"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="活动时间">
                <el-date-picker
                        v-model="form.apply_time"
                        type="datetime"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        placeholder="选择日期">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="活动地址">
                <el-input style="width: 300px" v-model="form.address" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="活动详细地址">
                <el-input style="width: 300px" v-model="form.location"
                          placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="人数上限制">
                <el-input-number
                        :min="0"
                        v-model="form.limit" controls-position="right"></el-input-number>
                <span class="hint">0 为无限制</span>
            </el-form-item>
            <el-form-item label="封面图">
                <image-select v-model="img" :url="form.picture"></image-select>
            </el-form-item>
            <el-form-item label="活动简介">
                <el-input
                        type="textarea"
                        style="width: 400px"
                        :rows="5"
                        v-model="form.content" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item label="推荐">
                <el-select class="header-col-select" v-model="form.recommend" placeholder="请选择">
                    <el-option
                            :value="1"
                            label="是">
                    </el-option>
                    <el-option
                            :value="0"
                            label="否">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="状态">
                <el-select class="header-col-select" v-model="form.status" placeholder="请选择">
                    <el-option
                            :value="1"
                            label="启用">
                    </el-option>
                    <el-option
                            :value="0"
                            label="禁用">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="审核方式">
                <el-select class="header-col-select" v-model="form.audit_method" placeholder="请选择">
                    <el-option
                            :value="1"
                            label="无需审核">
                    </el-option>
                    <el-option
                            :value="0"
                            label="人工审核">
                    </el-option>
                </el-select>
            </el-form-item>


            <div>
                <rich-view v-model="form.rich_text"></rich-view>
            </div>
            <el-form-item style="margin-top: 100px">
                <el-button type="primary" style="margin-left: 50px" :loading="isLoading"
                           @click="onSubmit">提交
                </el-button>
                <el-button @click="back">返回</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import api from '../../js/api/activity'

    export default {
        data() {
            return {
                imageUrl: '',
                form: {
                    recommend: 0,
                    audit_method: 0,
                    limit: 0,
                },
                isLoading: false,
                applyTimes: [],
                roles: {
                    account: [
                        {required: true, message: '请输入账号', trigger: 'blur'},
                        {min: 6, max: 12, message: '长度在 6 到 12 个字符', trigger: 'blur'}
                    ],
                },
                img: null,
            }
        },
        mounted() {
            if (this.$route.query.id !== undefined) {
                this.isNoEdit = true;
                this.loadData();
            } else {
                this.isNoEdit = false;
            }
        },
        methods: {
            loadData() {
                api.detail({id: this.$route.query.id}, res => {
                    this.form = res.data.data;
                    this.applyTimes = [this.form.begin_time, this.form.end_time];
                })
            },

            edit() {
                this.isNoEdit = false;
            },
            onSubmit() {
                if (!api.isStrValid(this.form.title)) {
                    this.$message.error('请输入活动名称');
                    return;
                }
                if (!api.isStrValid(this.form.apply_time)) {
                    this.$message.error('请选择活动日期');
                    return;
                }
                if (this.applyTimes.length !== 2) {
                    this.$message.error('请选择报名时间');
                    return;
                }

                this.form.begin_time = this.applyTimes[0];
                this.form.end_time = this.applyTimes[1];
                if (!api.isStrValid(this.form.address)) {
                    this.$message.error('请输入活动地址 ');
                    return;
                }
                if (!api.isStrValid(this.form.location)) {
                    this.$message.error('请输入活动详细地址 ');
                    return;
                }
                if (this.img == null && this.form.picture == null) {
                    this.$message.error('请选择图片');
                    return;
                }
                let data = api.objToFormData(this.form);
                data.append('picture', this.img);
                this.isLoading = true;
                api.save(data, res => {
                    this.isLoading = false;
                    this.$message.success(res.data.message);
                    this.$router.push({name: 'activity'})
                    this.$route.meta.keepAlive = false;
                }, () => {
                    this.isLoading = false;
                })

            },
            back() {
                this.$router.back();
            }

        }
    }
</script>
